import React, { useState, useEffect } from 'react'
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { Input, Table, Modal, Form, Button } from 'antd';
import Swal from "sweetalert2";
import { showDialog } from "../../utils/util";
import './Ssh.less'

import { getSshKey, createdSshKey, updateSshKey, deleteSshKey } from "../../api/account"

export default function Ssh() {
  const { TextArea } = Input;
  // 分页相关
  const [total, setTotal] = useState(200)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc'
  })

  useEffect(() => {
    getSshList()
  }, [params]);

  // 表格相关
  const [isTableLoading, setIsTableLoading] = useState(false)
  const [datasource, setDatasource] = useState([])
  const [activeId, setActiveId] = useState(0)

  const columns = [
    {
      title: '名称',
      width: 300,
      align: "left",
      dataIndex: 'name',
      className: "column-name"
    },
    {
      title: '指纹',
      dataIndex: 'finger_print',
      key: 'finger_print',
      width: 400,
      className: "finger-column"
    },
    {
      title: '操作',
      align: "left",
      width: 120,
      className: "cloumn-operate",
      fixed: "right",
      render: (_, record) => {
        return (
          <>
            {
              record.id === activeId ?
                <span className='more-operation'>
                  <a className='more-edit' onClick={() => SshEdit(record)} >编辑</a>
                  <a className='more-delete' onClick={() => SshDelete(record)} >删除</a>
                </span>
                :
                <span className='more' onClick={() => { setActiveId(record.id) }}>...</span>
            }
          </>
        )
      }
    }
  ]

  // modal 
  const [createSshForm] = Form.useForm();
  const [editSshForm] = Form.useForm();
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [isEditModalVisible, setIsEditModalVisible] = useState(false)
  const handleCancel = () => {
    setIsModalVisible(false);
  };

  // 编辑ssh
  const SshEdit = (record) => {
    // 给编辑表单赋值
    // 展示编辑表单
    editSshForm.setFieldsValue({
      id: record.id,
      name: record.name,
      public_key: record.public_key
    })
    setIsEditModalVisible(true)
  }

  // 删除ssh
  const SshDelete = (record) => {
    Swal.fire({
      text: `您将删除 ${record.name},一旦删除不可找回`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '确认',
      cancelButtonText: '取消',
      customClass: {
        icon: 'ssh-icon',
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 调用接口执行删除
        deleteSshKey(record).then(res => {
          if (res.data.status === 200) {
            if (res.data.msg === '删除成功') {
              Swal.fire({
                text: `成功删除 ${record.name}`,
                icon: 'success',
                confirmButtonColor: '#5C5EE4',
                confirmButtonText: '确认',
              })
              setIsModalVisible(false)
              getSshList()
            } else {
              showDialog(res.data.msg, "error");
            }
          }
        })

      }
    })
  }

  // 获取Ssh列表
  const getSshList = () => {
    setIsTableLoading(true)
    console.log(params);
    getSshKey(params).then(res => {
      console.log(res);
      if (res.data.status === 200) {
        let count = res.data.data.count;
        setTotal(() => {
          return count
        })
        let list = res.data.data.list
        setDatasource(list)
      }
      setIsTableLoading(false)
    })
  }

  // 换页
  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }

  // 创建ssh表单提交
  const createSsh = () => {
    const formData = createSshForm.getFieldsValue()
    //console.log(formData);
    createdSshKey({ ...formData }).then(res => {
      if (res.data.status === 200) {
        if (res.data.msg === '创建成功') {
          showDialog("创建成功", "success");
          setIsModalVisible(false)
          getSshList()
        } else {
          showDialog(res.data.msg, "error");
        }
      }
    })
  }

  // 编辑ssh表单提交
  const editSsh = () => {
    // 调用 ssh编辑 接口
    const formData = editSshForm.getFieldsValue()
    updateSshKey(formData).then(res => {
      if (res.data.status === 200) {
        showDialog("编辑成功", "success");
        setIsEditModalVisible(false)
        getSshList()
      }
    })
  }


  return (
    <div className='com-card ssh'>
      <div className="card-top">
        <div className="title">
          <p className='main-title'>SSH秘钥</p>
          <p className='lat-title'>秘钥将用于创建实例时使用，您可以使用您的私钥登录云服务器</p>
        </div>
        <Button className='create-btn' onClick={() => {
          setIsModalVisible(true)
          createSshForm.setFieldsValue({
            name: "",
            public_key: ""
          })
        }} ><img className='create-icon' src={require('../../assets/images/account/yao.png')} alt="" /> 创建秘钥</Button>
      </div>
      <div className="card-main">
        <div className="main_table">
          <Table columns={columns}
            dataSource={datasource}
            pagination={false}
            loading={isTableLoading}
            rowKey={record => record.id}

            scroll={datasource.length !== 0 ? {
              x: '100%',
              scrollToFirstRowOnChange: true
            } : null}
          />
        </div>
      </div>
      {/* 分页模块 */}
      <ComPagination total={total} page={params} changePage={changePage}></ComPagination>

      {/* 创建ssh */}
      <Modal
        visible={isModalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        width={"6.4rem"}
        maskClosable={true}
        onCancel={handleCancel}
        keyboard={true}
        getContainer={false}
      >
        <div className="modal-create-ssh">
          <div className="modal-title">
            创建SSH秘钥
          </div>
          <div className="modal-form">
            <Form
              colon={false}
              size="large"
              form={createSshForm}
              onFinish={createSsh}
            >
              <Form.Item label="名称" name="name"
                rules={[
                  {
                    required: true,
                    message: "请输入名称",
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item label="公钥" name="public_key"
                rules={[
                  {
                    required: true,
                    message: "请输入公钥",
                  },
                ]}
              >
                <TextArea rows={8} />
              </Form.Item>
              <Form.Item noStyle>
                <div className="modal-form-footer flex">
                  <Button className='btn-ok' size='middle' htmlType="submit">确定</Button>
                  <Button className='btn-no' size='middle' onClick={() => setIsModalVisible(false)}>取消</Button>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>

      {/* 编辑ssh */}
      <Modal
        visible={isEditModalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        width={"6.4rem"}
        maskClosable={true}
        onCancel={handleCancel}
        keyboard={true}
        getContainer={false}
      >
        <div className="modal-create-ssh">
          <div className="modal-title">
            编辑SSH秘钥
          </div>
          <div className="modal-form">
            <Form
              colon={false}
              size="large"
              form={editSshForm}
              onFinish={editSsh}
            >
              <Form.Item name="id" hidden>
                <Input />
              </Form.Item>
              <Form.Item label="名称" name="name"
                rules={[
                  {
                    required: true,
                    message: "请输入名称",
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item label="公钥" name="public_key"
                rules={[
                  {
                    required: true,
                    message: "请输入公钥",
                  },
                ]}
              >
                <TextArea rows={8} />
              </Form.Item>
              <Form.Item noStyle>
                <div className="modal-form-footer flex">
                  <Button className='btn-ok' size='middle' htmlType="submit">确定</Button>
                  <Button className='btn-no' size='middle' onClick={() => setIsEditModalVisible(false)}>取消</Button>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>
    </div>
  )
}
