import request from '../utils/request'
/* 概要 */
// 账户详情
export function getAccount () {
  return request.get(`/account`)
}
// 修改账户
export function updateAccount (params) {
  return request.put(`/account`, params)
}
// 验证原手机
export function verifiedPhone (params) {
  return request.put(`/account/phone/old`, params)
}
// 修改手机
export function updatePhone (params) {
  return request.put(`/account/phone`, params)
}
// 验证原邮箱
export function verifiedEmail (params) {
  return request.put(`/account/email/old`, params)
}
// 修改邮箱
export function updateEmail (params) {
  return request.put(`/account/email`, params)
}
// 修改密码
export function updatePassword (params) {
  return request.put(`/account/password`, params)
}

/* ssh */
export function getSshKey (params) {
  return request.get(`/ssh_key`, {params})
}
export function createdSshKey (params) {
  return request.post(`/ssh_key`, params)
}
export function updateSshKey (params) {
  return request.put(`/ssh_key/${params.id}`, params)
}
export function deleteSshKey (params) {
  return request.delete(`/ssh_key/${params.id}`, params)
}


/* api */
export function getApi (params) {
  return request.get(`/api`, {params:{...params} })
}
export function createdApi (params) {
  return request.post(`/api`, params)
}
export function deleteApi (params) {
  return request.delete(`/api/${params}`)
}
// api白名单
export function updateWhiteList (params) {
  return request.put(`/api/${params.id}/white_list`, params)
}

/* 操作日志 */
export function getApiLog (params) {
  return request.get(`/api/log`, { params })
}
export function getLog (params) {
  return request.get(`/log`, { params })
}